.kasa-banner {
    display: flex;
    margin-left: 7%;
    margin-right: 7%;
    margin-top: 40px;
}

.figure_logo {
    flex-grow: 3;
    margin: 0;
}

.lien-pages-box {
    display: flex;
    flex-grow: 1;
    justify-content: space-around;
    align-items: center;
}

.nav_link {
    color: #FF6060;
    font-size: 24px;
    text-decoration: none;
    font-weight: 500;
}

@media (max-width: 660px) {

    .nav_link {
        font-size: 18px;
    }

    .lien-pages-box {
        display: flex;
        width: 50%;
    }

    .figure_logo {
        width: 40%;
    }

    .logo_img {
        width: 75%;
        height: auto;
    }
  }