.footer {
    background-color: black;
    width: 100%;
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left:0;
    bottom:0;
    right:0;
}

.figure-logo {
    padding-top: 20px;
}

.logo-img {
    width: 122px;
}

.h4_footer {
    color: white;
    font-size: 24px;
}

@media (max-width: 660px) {

    .h4_footer {
        font-size: 1rem;
    }

  }