.gallery-box {
    background-color: #F6F6F6;
    width: 86.1%;
    margin: auto;
    margin-top: 43px;
    margin-bottom: 43px;
    border-radius: 25px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

@media (max-width: 615px) {

    .gallery-box {
        background-color: white;
    }

  }